import React from 'react'
import { CustomTabs } from '../../../reuseableComponents';
import KPIList from './KPIList';
import QuarterlyAndAnnualRating from './QuarterlyAndAnnualRating';

const MyKRA = () => {
    const tableTabsRequest = [
        { name: "KPI List", onClickFun: (id) => getMyKpiData(id), id: "kpiList" },
        { name: "Quarterly and Annual Rating", onClickFun: (id) => getMyKpiData(id), id: "quarterlyAndAnnualRating" },

    ];
    const getMyKpiData = () => {

    }
    return (
        <div>
            <div className="ticketScoreSection">
                <div className="cardBox myRequest">
                    <div className="cardHead d-flex justify-content-between">
                        <h3>
                            <img
                                src="images/kpi-guidline.svg"
                                alt=""
                                width={40}
                                className="me-2 fw-bold"
                            />{" "}
                            My KPI
                        </h3>
                        <div className="selectDate">
                            <select
                                className="form-select form-select-sm"
                                aria-label="Small select example"
                            >
                                <option selected="">Select Financial year</option>
                                <option value={1}>April-22 To March-23</option>
                                <option value={2}>April-23 To March-24</option>
                            </select>
                        </div>
                    </div>
                    <CustomTabs tabs={tableTabsRequest} type="myKRA" />
                    
                    <div className="tab-content mykpiMain">
                        <div className="tab-pane fade active show" id="kpiList">
                           <KPIList/>
                        </div>
                        <div className="tab-pane fade " id="quarterlyAndAnnualRating">
                            <QuarterlyAndAnnualRating/>
                        </div>
                    </div>
                </div>
            </div>

        </div>
    )
}

export default MyKRA
